.cell-not-draggable-padding {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}
.cell-not-draggable.cell-not-draggable-padding {
  border-top: none;
  border-bottom: none;
}
.edit-panel-wrapper {
  page-break-after: 5;
  // border: brown 1px solid;
  display: flex;
  flex-direction: column;
  padding-top: 5px;
}

.expanderBox_shrink {
  max-height: 0;
  transition: max-height 0.2s;
  overflow: hidden;
  border: none !important;
  transition: border 0.2s;
}
.expanderBox_expand {
  max-height: 5000px;
  transition: max-height 0.6s;
}
